.point-img{
    width: 10%;
    height: auto;  
    margin-left: 105px; 
    float: left;
}

.point-p{
    padding: 5px;  
}

.ProducList-point{
   margin-left: 15px;
   border-radius: 30px;
   margin-top: 18px;
   width: 60%;
   background-color: khaki; 
}

.ProducList-button{
    background-color: white;
    border: 0px;
    float: right;
    width: 25%;  
    margin-top: 2px;
    margin-right: 10px;
}

.product-img{
    float: right;
    width: 100%;
    height:auto ;
    margin-right: 3px;
}

.hr2{
    width: 86%;
    margin-left: 13px;
}

.ProducList-category{
    margin: 0px;
    text-align: left;
    margin-left: 13px;
}

.ProducList-name{
    padding-bottom: 10px;
    text-align: left;
    margin-left:13px; 
    font-size: 16px; 
    font-weight: bold;
    margin-top: 4px;
}

.Category-container{
    color:#203A43; 
    text-align: center;
    font-family: Georgia;
    font-weight: 0.5;
    font-size: 1em;
    border-color:#F2F2F2;
    width:20%;
    height:auto;
    position:relative;
    margin:10px;
    left: 80px;
    top: 15px;
    float: left;
    border-style:outset;
    border-width: 2px;
}

.ProducList-img{
    width: 100%;
    height: auto;
}

@media screen and (max-width: 800px) {   
    .Category-container{
        width:40%;
        height:auto;
        position:relative;
        left: 50px;
        top: 10px;
        margin: 20px;  
    } 
}

@media (max-width: 400px) { 
    .Category-container{
        width:80%;
        height:auto;
        position:relative;
        left: 30px;
        top: 10px;   
    } 
}





